* {
    font-weight: 300;
}

@media screen and (min-width:1200px) {
    .shop-container {
        display: grid;
        grid-template-columns: 4fr 1fr;
        /* margin: 100px 50px; */
    }
}

.products-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 45px;
    padding: 100px 50px;
}

@media screen and (min-device-width:) and (max-device-width:1025px) {
    .products-container {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (max-width:650px) {
    .products-container {
        grid-template-columns: repeat(1, 1fr);
        place-items: center;
    }
}

/* ! Cart container */
.cart-container {
    margin-top: -16px;
}

.cart-container>div>h1 {
    text-align: center;
    font-weight: bolder;
}

.gradient-background {
    background: linear-gradient(450deg, #ffc290, #faa777, #fab67e);
    background-size: 180% 180%;
    animation: gradient-animation 6s ease infinite;
    -webkit-animation: gradient-animation 6s ease infinite;
}

@keyframes gradient-animation {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}