@import url('https://fonts.googleapis.com/css2?family=Alegreya+Sans+SC:wght@300;400;500&family=Exo:wght@100;200;400;500;700&family=Michroma&family=Play&family=Space+Mono&display=swap');

#companyName {
    font-family: 'Michroma', sans-serif !important;
    color: #f5a571;
    cursor: pointer;
    text-decoration-line: underline;
    text-decoration-style: "solid";
    text-decoration-color: rgba(250, 86, 10, 0.71);
    text-decoration-thickness: 9px;
}

* {
    margin: 0;
}

.header {
    background-color: #16213E;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 80px;
    padding-right: 80px;
    margin-bottom: 1rem;
    padding-top: 10px;
    padding-bottom: 10px;
}

.linkContainer>a {
    font-weight: bolder;
}

/* & CSS for max width 600px, #minSizeScreen */
#minSizeScreen {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 27px;
}

#expandLessBtn {
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    padding: 20px;
}

.buttonExpand>div>a {
    color: ghostwhite;
    font-weight: bolder;
}

.hoverMin {
    --b: 0.1em;
    --c: #e4eef1;

    color: ghostwhite;
    padding-block: var(--b);
    background:
        linear-gradient(var(--c) 50%, #000 0) 0% calc(100% - var(--_p, 0%))/100% 200%,
        linear-gradient(var(--c) 0 0) 0% var(--_p, 0%)/var(--_p, 0%) var(--b) no-repeat;
    -webkit-background-clip: text, padding-box;
    background-clip: text, padding-box;
    transition: .3s var(--_s, 0s) linear, background-size .3s calc(.3s - var(--_s, 0s));
}

.hoverMin:hover {
    --_p: 100%;
    --_s: .3s;
    --c: black;
    color: ghostwhite;
}


/* & */

a {
    text-decoration: none;
    color: ghostwhite;
    margin-left: 35px;
}

.hover {
    --b: 0.1em;
    --c: #e4eef1;

    color: ghostwhite;
    padding-block: var(--b);
    background:
        linear-gradient(var(--c) 50%, #000 0) 0% calc(100% - var(--_p, 0%))/100% 200%,
        linear-gradient(var(--c) 0 0) 0% var(--_p, 0%)/var(--_p, 0%) var(--b) no-repeat;
    -webkit-background-clip: text, padding-box;
    background-clip: text, padding-box;
    transition: .3s var(--_s, 0s) linear, background-size .3s calc(.3s - var(--_s, 0s));
}

.hover:hover {
    --_p: 100%;
    --_s: .3s;
    --c: orange;
    color: orange;
}

#login {
    display: inline-flex;
    flex-direction: row;
    align-items: center;
}