@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400&family=Quicksand:wght@300;400;500;600&family=Raleway:wght@100;200;400;500;600;700&family=Space+Grotesk:wght@300;400;500;600;700&display=swap');

.main-container {
    display: flex;
    justify-content: center;
}

.cart {
    position: fixed;
    padding: 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 70vh;
    margin: 25px;
}

.firstDiv>h1 {
    font-family: 'Rubik', sans-serif !important;
    font-weight: 600;
}

.middleDiv {
    padding: 8px;
    color: white;
    font-weight: 600;
}

.middleDiv>h3 {
    font-family: 'Space Grotesk', sans-serif !important;
    font-weight: 600;
}

.glass {
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border-radius: 20px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
}

.purchaseBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    cursor: pointer;
    border: none;
    padding: 12px;
    margin-top: 10px;
    background-color: #FF7200;
    color: ghostwhite;
    font-weight: bolder;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    transition: .4s;
    -webkit-transition: .4s;
    -moz-transition: .4s;
    -ms-transition: .4s;
    -o-transition: .4s;
}

.purchaseBtn:hover {
    -webkit-box-shadow: 0px 0px 18px 4px rgba(255, 115, 0, 0.9);
    -moz-box-shadow: 0px 0px 18px 4px rgba(255, 115, 0, 0.9);
    box-shadow: 0px 0px 18px 4px rgba(255, 115, 0, 0.9);
}