@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400&family=Quicksand:wght@300;400;500;600&family=Raleway:wght@100;200;400;500;600;700&family=Space+Grotesk:wght@300;400;500;600;700&display=swap');

.product {
    width: 310px;
    height: 500px;
    margin-bottom: 1em;
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.inner_product {
    padding: 12px;
}

#productTag {
    cursor: pointer;
    font-family: 'Raleway', sans-serif !important;
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 5px;
}

img {
    height: 286px;
    width: 286px;
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
}

.cartBtn {
    border: none;
    cursor: pointer;
    background-color: #FF7200;
    display: flex;
    justify-content: center;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    padding: 10px;
    transition: .3s;
    -webkit-transition: .3s;
    -moz-transition: .3s;
    -ms-transition: .3s;
    -o-transition: .3s;
}

.cartBtn:hover {
    -webkit-box-shadow: 0px 0px 18px 4px rgba(255, 115, 0, 0.9);
    -moz-box-shadow: 0px 0px 18px 4px rgba(255, 115, 0, 0.9);
    box-shadow: 0px 0px 18px 4px rgba(255, 115, 0, 0.9);
}

.cartBtn>button {
    color: ghostwhite;
    border: none;
    background-color: inherit;
    text-decoration: none;
    font-size: 15px;
    font-weight: 400;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 5px;
}

.effect {
    background: linear-gradient(135deg, rgba(235, 237, 240, 0.274), rgba(239, 237, 241, 0.445));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border-radius: 20px;
    border: none;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}